import common from '../../../common/common.js'

export default {
	data() {
		return {
			info:[],				//主账号信息
			rechargeList:[],
			isall:0,
			page:0,
			type:'wallet',
			current:0,			//转出类型 0:不可提现账户 1:可提现账户
			amounts:0,			//转出金额
			useraccount:'',		//转出账户
			popzhuan:false,		
			pingjun:0,			//平均每天单数
			needpingjum:10,		//需每天平均单数
			userInfo:[],
		}
	},
	onLoad() {
		var that = this
		var shopInfo = uni.getStorageSync('shopInfo');
		var utype =  uni.getStorageSync('utype');			//用户类型
		
		that.shopInfo = shopInfo
		that.needpingjum = shopInfo.shop_pingjundan
		
		that.getMerchantInfo();
		that.getRechargeList();

	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');
		that.page = 0;
		that.isall = 0;
		that.rechargeList = []
		
		that.getMerchantInfo();
		that.getRechargeList();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		var that = this
		uni.showNavigationBarLoading();			
		
		that.getRechargeList();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods: {
		toggleZhuan(){
			var that = this
			
			if(!that.popzhuan){
				var account = that.info.shop_account
				if(account == 0){
					uni.showToast({
						title:'余额不足',
						icon:'none',
						duration:1500
					})
					return false
				}
			}
			that.popzhuan = !that.popzhuan
		},
		checkAmounts(e){
			var that = this
			that.amounts = e.detail.value				
		},
		checkUserAccount(e){
			var that = this
			that.useraccount = e.detail.value				
		},
		submitSave(){
			var that = this
			var account = that.info.shop_account
			var useraccount = that.useraccount
			var amounts = parseFloat(that.amounts)
			var current = that.current
			var shopInfo = uni.getStorageSync('shopInfo');
			if(useraccount == ''){
				uni.showToast({
					title:'请输入微信/支付宝账户',
					icon:'none',
					duration:1500
				})
				return false
			}
			if(amounts == 0){
				uni.showToast({
					title:'请输入提现金额',
					icon:'none',
					duration:1500
				})
				return false
			}
			
			if(amounts > account){
				uni.showToast({
					title:'余额不足',
					icon:'none',
					duration:1500
				})
				return false
			}
			
			uni.request({
				url: common.sjApiUrl + 'merchant/zhuanAccount',
				method: 'GET',
				data: {
					shopid:shopInfo.shop_id,
					useraccount:useraccount,
					amounts:amounts,
					current:current
				},
				success: res => {
					if(res.data.code == 0){
						uni.showToast({
							title:'操作成功',
							icon:'success',
							duration:1500,
							success:res=>{
								setTimeout(function(){
									that.resetAttr();
									that.getMerchantInfo();
									that.getData();
									that.toggleZhuan();
								})
							}
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none',
							duration:1500									
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		radioChange: function(evt) {
			var that = this
			that.current = evt.target.value;
			console.log(that.current);
		},
		//切换账户状态
		changeAccountType(e){
			var that = this
			var t_type = e.currentTarget.dataset.type
			if(t_type == that.type){
				return false
			}else{
				that.type = t_type
				that.resetAttr();
				that.getData();
			}
		},
		resetAttr(){
			var that = this
			that.page = 0
			that.isall = 0
			that.rechargeList = []
		},
		getData(){
			var that = this
			var v = that.type
			that.getRechargeList();
		},
		//获取店铺主账号信息
		getMerchantInfo(){
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
			uni.request({
				url: common.sjApiUrl + 'merchant/getMerchantInfo',
				method: 'GET',
				data: {
					shop_id:shopInfo.shop_id
				},
				success: res => {
										
					that.info = res.data.info
					that.pingjun = res.data.pingjun
					console.log("infos",that.info)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取店铺可提资金资金明细列表
		getAccountRechargeList(){
			var that = this
			var isall = that.isall
			console.log('isall',isall)
			if(isall == 1){
				return false;
			}
			var shopInfo = uni.getStorageSync('shopInfo');
			uni.request({
				url: common.sjApiUrl + 'merchant/getAccountRechargeList',
				method: 'GET',
				data: {
					shopid:shopInfo.shop_id,
					page:that.page+1
				},
				success: res => {
					that.rechargeList = that.rechargeList.concat(res.data.recharge_list);
					that.isall = res.data.isall;
					console.log(res.data.recharge_list);
					that.page = res.data.page
					console.log(that.page);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取店铺返现资金明细列表
		getMoneyRechargeList(){
			var that = this
			var isall = that.isall
			console.log('isall',isall)
			if(isall == 1){
				return false;
			}
			var shopInfo = uni.getStorageSync('shopInfo');
			uni.request({
				url: common.sjApiUrl + 'merchant/getMoneyRechargeList',
				method: 'GET',
				data: {
					shopid:shopInfo.shop_id,
					page:that.page+1
				},
				success: res => {
					that.rechargeList = that.rechargeList.concat(res.data.recharge_list);
					that.isall = res.data.isall;
					console.log(res.data.recharge_list);
					that.page = res.data.page
					console.log(that.page);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取店铺返现资金明细列表
		getCashRechargeList(){
			var that = this
			var isall = that.isall
			console.log('isall',isall)
			if(isall == 1){
				return false;
			}
			var shopInfo = uni.getStorageSync('shopInfo');
			uni.request({
				url: common.sjApiUrl + 'merchant/getCashRechargeList',
				method: 'GET',
				data: {
					shopid:shopInfo.shop_id,
					page:that.page+1
				},
				success: res => {
					that.rechargeList = that.rechargeList.concat(res.data.recharge_list);
					that.isall = res.data.isall;
					console.log(res.data.recharge_list);
					that.page = res.data.page
					console.log(that.page);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取资金明细列表
		getRechargeList(){
			var that = this
			var isall = that.isall
			console.log('isall',isall)
			if(isall == 1){
				return false;
			}
			var shopInfo = uni.getStorageSync('shopInfo');
			var uid = shopInfo.shop_id
			uni.request({
				url: common.sjApiUrl + 'merchant/getRechargeList',
				method: 'GET',
				data: {
					uid:uid,
					page:that.page+1
				},
				success: res => {
					that.rechargeList = that.rechargeList.concat(res.data.recharge_list);
					that.isall = res.data.isall;
					console.log("recharge_list",res.data.recharge_list);
					that.page = res.data.page
					console.log(that.page);
				},
				fail: () => {},
				complete: () => {}
			});
		}
		
	}
}
